.experience-section {
  padding: 80px 10%;
  background-color: #f8fbff;
  text-align: center;
}

.experience-section h2 {
  font-size: 2.2rem;
  color: #007acc;
  margin-bottom: 50px;
  position: relative;
}

.experience-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.experience-card {
  background-color: #ffffff;
  border: 2px solid #cce5ff;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 122, 204, 0.1);
  padding: 25px;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.experience-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 8px 16px rgba(0, 122, 204, 0.2);
}

.experience-card h3 {
  color: #004b8d;
  margin-bottom: 5px;
}

.experience-card h4 {
  color: #007acc;
  font-size: 1.05rem;
  margin-bottom: 5px;
}

.duration {
  color: #555;
  font-size: 0.9rem;
  margin-bottom: 10px;
}

.details {
  color: #333;
  font-size: 0.95rem;
  line-height: 1.6;
}

.subheading {
  font-size: 1.8rem;
  color: #004b8d;
  margin-bottom: 30px;
  border-top: 2px solid #cce5ff;
  display: inline-block;
  padding-top: 20px;
}

.cert-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
}

.cert-card {
  background-color: #ffffff;
  border: 1.5px solid #cce5ff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 3px 6px rgba(0, 122, 204, 0.1);
  transition: transform 0.3s ease;
}

.cert-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 12px rgba(0, 122, 204, 0.15);
}

.cert-card h4 {
  color: #004b8d;
  font-size: 1.1rem;
  margin-bottom: 8px;
}

.cert-card p {
  color: #555;
  font-size: 0.9rem;
  margin-bottom: 10px;
}

.cert-card a {
  text-decoration: none;
  color: #007acc;
  font-weight: 600;
  border: 1px solid #007acc;
  padding: 5px 12px;
  border-radius: 6px;
  transition: all 0.3s ease;
}

.cert-card a:hover {
  background-color: #007acc;
  color: #fff;
}
