.skills-section {
  background-color: #f8f9fc; /* soft off-white background */
  color: #2d3436; /* dark gray text */
  padding: 80px 50px;
  text-align: center;
}

.skills-section h2 {
  font-size: 2.5rem;
  margin-bottom: 40px;
  color: #0056b3; /* professional blue tone */
  letter-spacing: 1px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 30px;
  justify-items: center;
}

.skill-card {
  background-color: #ffffff;
  border: 2px solid #e0e0e0;
  border-radius: 15px;
  width: 170px;
  height: 170px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.skill-card:hover {
  transform: translateY(-6px);
  border-color: #007bff;
  box-shadow: 0 6px 18px rgba(0, 123, 255, 0.2);
  background-color: #f0f8ff;
}

.skill-card .icon {
  font-size: 3rem;
  margin-bottom: 10px;
  color: #007bff;
}

.skill-card h4 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: #2d3436;
}

@media (max-width: 768px) {
  .skills-section {
    padding: 60px 20px;
  }
}
