.projects-section {
  background-color: #f8f9fc;
  color: #2d3436;
  padding: 80px 50px;
  text-align: center;
}

.projects-section h2 {
  font-size: 2.5rem;
  color: #007bff;
  margin-bottom: 40px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.project-card {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 18px rgba(0, 123, 255, 0.2);
}

.project-card img {
  width: 100%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.project-card .content {
  padding: 20px;
}

.project-card h3 {
  color: #007bff;
  font-size: 1.3rem;
}

.project-card p {
  color: #6c757d;
  margin-bottom: 15px;
}

.project-links a {
   color: white;
  text-decoration: none;
  background: #58a6ff;
  padding: 8px 16px;
  border-radius: 8px;
  display: inline-block;
  margin-top: 10px;
  transition: 0.3s;
}

.project-links a:hover {
  text-decoration: underline;
}
