.home-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 80px 100px;
  background-color: #f8f9fc;
  color: #2d3436;
  min-height: 100vh;
}

.intro {
  max-width: 600px;
}

.intro h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.intro h2 span {
  color: #007bff;
}

.intro h3 {
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 20px;
  color: #6c757d;
}

.intro p {
  line-height: 1.6;
  margin-bottom: 30px;
  color: #6c757d;
}

.btn {
  background-color: #007bff;
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: background-color 0.3s;
}

.btn:hover {
  background-color: #0056b3;
}

.image img {
  width: 280px;
  height: 280px;
  border-radius: 50%;
  border: 4px solid #007bff;
  object-fit: cover;
}

@media (max-width: 768px) {
  .home-section {
    flex-direction: column;
    text-align: center;
    padding: 50px 20px;
  }

  .image {
    margin-top: 30px;
  }
}
